"use client";

import { useState } from "react";
import { useForm, Controller } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { User, Mail, Lock, Eye, EyeOff, Loader2, Phone } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Checkbox } from "@/components/ui/checkbox";
import { useAuthStore } from "@/store/authStore";
import PasswordStrength from "./PasswordStrength";

// Schema للتحقق من البيانات
const signupSchema = z
  .object({
    name: z
      .string()
      .min(1, "الاسم مطلوب")
      .min(3, "الاسم يجب أن يكون 3 أحرف على الأقل"),
    email: z
      .string()
      .min(1, "البريد الإلكتروني مطلوب")
      .email("البريد الإلكتروني غير صحيح"),
    phone: z
      .string()
      .min(1, "رقم الجوال مطلوب")
      .regex(/^(05|5)[0-9]{8}$/, "رقم الجوال غير صالح (يجب أن يبدأ بـ 05 ويتكون من 10 أرقام)"),
    password: z
      .string()
      .min(1, "كلمة المرور مطلوبة")
      .min(8, "كلمة المرور يجب أن تكون 8 أحرف على الأقل"),
    confirmPassword: z.string().min(1, "تأكيد كلمة المرور مطلوب"),
    acceptTerms: z.boolean().refine((val) => val === true, {
      message: "يجب الموافقة على الشروط والأحكام",
    }),
  })
  .refine((data) => data.password === data.confirmPassword, {
    message: "كلمة المرور غير متطابقة",
    path: ["confirmPassword"],
  });

type SignupFormData = z.infer<typeof signupSchema>;

export default function SignupForm() {
  const router = useRouter();
  const { signup, isLoading, error, clearError } = useAuthStore();
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  const {
    register,
    handleSubmit,
    watch,
    control,
    formState: { errors },
  } = useForm<SignupFormData>({
    resolver: zodResolver(signupSchema),
    defaultValues: {
      name: "",
      email: "",
      phone: "",
      password: "",
      confirmPassword: "",
      acceptTerms: false,
    },
  });

  const password = watch("password");

  const onSubmit = async (data: SignupFormData) => {
    clearError();
    
    try {
      await signup(data);
      router.push("/dashboard");
    } catch (error) {
      console.error("Signup error:", error);
    }
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="space-y-5">
      {/* رسالة الخطأ */}
      {error && (
        <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-lg text-sm">
          {error}
        </div>
      )}

      {/* الاسم الكامل */}
      <div className="space-y-2">
        <Label htmlFor="name" className="text-gray-700 font-medium">
          الاسم الكامل
        </Label>
        <div className="relative">
          <User className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
          <Input
            id="name"
            type="text"
            placeholder="محمد أحمد"
            className="pr-10 h-12"
            {...register("name")}
            disabled={isLoading}
          />
        </div>
        {errors.name && (
          <p className="text-red-600 text-sm">{errors.name.message}</p>
        )}
      </div>

      {/* البريد الإلكتروني */}
      <div className="space-y-2">
        <Label htmlFor="email" className="text-gray-700 font-medium">
          البريد الإلكتروني
        </Label>
        <div className="relative">
          <Mail className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
          <Input
            id="email"
            type="email"
            placeholder="example@eventmeena.com"
            className="pr-10 h-12"
            {...register("email")}
            disabled={isLoading}
          />
        </div>
        {errors.email && (
          <p className="text-red-600 text-sm">{errors.email.message}</p>
        )}
      </div>

      {/* رقم الجوال */}
      <div className="space-y-2">
        <Label htmlFor="phone" className="text-gray-700 font-medium">
          رقم الجوال
        </Label>
        <div className="relative">
          <Phone className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
          <Input
            id="phone"
            type="tel"
            placeholder="0501234567"
            className="pr-10 h-12"
            {...register("phone")}
            disabled={isLoading}
          />
        </div>
        {errors.phone && (
          <p className="text-red-600 text-sm">{errors.phone.message}</p>
        )}
      </div>

      {/* كلمة المرور */}
      <div className="space-y-2">
        <Label htmlFor="password" className="text-gray-700 font-medium">
          كلمة المرور
        </Label>
        <div className="relative">
          <Lock className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
          <Input
            id="password"
            type={showPassword ? "text" : "password"}
            placeholder="••••••••"
            className="pr-10 pl-10 h-12"
            {...register("password")}
            disabled={isLoading}
          />
          <button
            type="button"
            onClick={() => setShowPassword(!showPassword)}
            className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600 transition-colors"
            disabled={isLoading}
          >
            {showPassword ? (
              <EyeOff className="w-5 h-5" />
            ) : (
              <Eye className="w-5 h-5" />
            )}
          </button>
        </div>
        {errors.password && (
          <p className="text-red-600 text-sm">{errors.password.message}</p>
        )}
        
        {/* مؤشر قوة كلمة المرور */}
        <PasswordStrength password={password} />
      </div>

      {/* تأكيد كلمة المرور */}
      <div className="space-y-2">
        <Label htmlFor="confirmPassword" className="text-gray-700 font-medium">
          تأكيد كلمة المرور
        </Label>
        <div className="relative">
          <Lock className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
          <Input
            id="confirmPassword"
            type={showConfirmPassword ? "text" : "password"}
            placeholder="••••••••"
            className="pr-10 pl-10 h-12"
            {...register("confirmPassword")}
            disabled={isLoading}
          />
          <button
            type="button"
            onClick={() => setShowConfirmPassword(!showConfirmPassword)}
            className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600 transition-colors"
            disabled={isLoading}
          >
            {showConfirmPassword ? (
              <EyeOff className="w-5 h-5" />
            ) : (
              <Eye className="w-5 h-5" />
            )}
          </button>
        </div>
        {errors.confirmPassword && (
          <p className="text-red-600 text-sm">{errors.confirmPassword.message}</p>
        )}
      </div>

      {/* الموافقة على الشروط */}
      <div className="flex items-start gap-2">
        <Controller
          name="acceptTerms"
          control={control}
          render={({ field }) => (
            <Checkbox
              id="acceptTerms"
              checked={field.value}
              onCheckedChange={field.onChange}
              className="mt-1"
            />
          )}
        />
        <Label
          htmlFor="acceptTerms"
          className="text-sm text-gray-600 cursor-pointer leading-relaxed"
        >
          أوافق على{" "}
          <Link href="/terms" className="text-primary hover:text-blue-700 font-medium">
            الشروط والأحكام
          </Link>{" "}
          و{" "}
          <Link href="/privacy" className="text-primary hover:text-blue-700 font-medium">
            سياسة الخصوصية
          </Link>
        </Label>
      </div>
      {errors.acceptTerms && (
        <p className="text-red-600 text-sm">{errors.acceptTerms.message}</p>
      )}

      {/* زر إنشاء الحساب */}
      <Button
        type="submit"
        className="w-full h-12 text-base font-semibold"
        disabled={isLoading}
      >
        {isLoading ? (
          <>
            <Loader2 className="w-5 h-5 ml-2 animate-spin" />
            جاري إنشاء الحساب...
          </>
        ) : (
          "إنشاء حساب"
        )}
      </Button>

      {/* رابط تسجيل الدخول */}
      <p className="text-center text-sm text-gray-600">
        لديك حساب بالفعل؟{" "}
        <Link
          href="/login"
          className="text-primary font-semibold hover:text-blue-700 transition-colors"
        >
          تسجيل الدخول
        </Link>
      </p>
    </form>
  );
}

