"use client";

import { PasswordStrength as PasswordStrengthType } from "@/types/auth";

interface PasswordStrengthProps {
  password: string;
}

// حساب قوة كلمة المرور
const calculatePasswordStrength = (password: string): { strength: PasswordStrengthType; score: number; feedback: string } => {
  if (!password) {
    return { strength: "weak", score: 0, feedback: "" };
  }

  let score = 0;
  
  // الطول
  if (password.length >= 8) score++;
  if (password.length >= 12) score++;
  
  // أحرف صغيرة
  if (/[a-z]/.test(password)) score++;
  
  // أحرف كبيرة
  if (/[A-Z]/.test(password)) score++;
  
  // أرقام
  if (/[0-9]/.test(password)) score++;
  
  // رموز خاصة
  if (/[^A-Za-z0-9]/.test(password)) score++;

  // تحديد القوة
  let strength: PasswordStrengthType;
  let feedback: string;

  if (score <= 2) {
    strength = "weak";
    feedback = "ضعيفة";
  } else if (score <= 3) {
    strength = "medium";
    feedback = "متوسطة";
  } else if (score <= 4) {
    strength = "strong";
    feedback = "قوية";
  } else {
    strength = "very-strong";
    feedback = "قوية جداً";
  }

  return { strength, score, feedback };
};

export default function PasswordStrength({ password }: PasswordStrengthProps) {
  const { strength, score, feedback } = calculatePasswordStrength(password);

  if (!password) return null;

  // الألوان حسب القوة
  const colors = {
    weak: "bg-red-500",
    medium: "bg-orange-500",
    strong: "bg-green-500",
    "very-strong": "bg-blue-600",
  };

  const textColors = {
    weak: "text-red-600",
    medium: "text-orange-600",
    strong: "text-green-600",
    "very-strong": "text-blue-700",
  };

  // عدد الأعمدة المملوءة (من 4)
  const filledBars = Math.ceil((score / 6) * 4);

  return (
    <div className="space-y-2 mt-2">
      {/* شريط القوة */}
      <div className="flex gap-1">
        {[1, 2, 3, 4].map((bar) => (
          <div
            key={bar}
            className={`h-1.5 flex-1 rounded-full transition-all duration-300 ${
              bar <= filledBars ? colors[strength] : "bg-gray-200"
            }`}
          />
        ))}
      </div>

      {/* النص */}
      <p className={`text-xs font-medium ${textColors[strength]}`}>
        قوة كلمة المرور: {feedback}
      </p>

      {/* نصائح */}
      {strength === "weak" && (
        <ul className="text-xs text-gray-600 space-y-1 mt-2">
          <li>• استخدم 8 أحرف على الأقل</li>
          <li>• أضف أحرف كبيرة وصغيرة</li>
          <li>• أضف أرقام ورموز خاصة</li>
        </ul>
      )}
    </div>
  );
}

